$(".burger").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})

$(".desktop-burger").click(function () {
    $(".header").toggleClass("_open-desktop")
})

$(document).mouseup( function(e){
    let div = $( ".header" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open-desktop")){
            $(div).removeClass("_open-desktop")
        }
    }
});
